import {mapState,mapMutations, mapActions, mapGetters} from 'vuex'
import navbar from './navbar.vue'
export default {
  data() {
    return {
      tags: "師培、教具、國小、偏鄉、國中、高中、大學、實驗教育、媒體".split("、"),
      tempSearchKeyword: ""
    }
  },
  computed: {
    ...mapState({
      menuState: state=>state.menuState,
      posts: state => state.post.posts,
      searchKeyword: state=>state.searchKeyword,
      menuType: state=>state.menuType,
      mobile: state=>state.mobile,
      auth: state=>state.auth
    }),
    filteredPost(){
      return this.posts.map(o=>({...o,tag: "ZA EXPO"})).filter(o=>JSON.stringify(o).indexOf(this.searchKeyword)!=-1)
    }
  },
  methods: {
    ...mapMutations(['setMenuState','setSearchKeyword','openMenu']),
    ...mapActions({
      register: 'auth/register',
      login: 'auth/login',
      logout: 'auth/logout',
      loginFacebook: "auth/loginFacebook",
      authInit: "auth/init"
    }),
    ...mapGetters({
      getUserPhoto: 'auth/getUserPhoto',
      isAdmin: 'auth/isAdmin'
    }),
    postTarget(post){
      // if (this.$route.meta.type=="expo"){
        return `/expo/${post.year}/blog/${post.id}`
      // }

      // if (this.$route.meta.type=="news"){
      //   return `/news/${post.id}`
      // }
    },
    hambergurAction(){
      if (!this.menuState){
        this.openMenu('nav')
      }else{
        this.setMenuState(false)
      }
    }
  },
  mounted(){
    // this.authInit()
  },
  watch: {
    tempSearchKeyword(){
      this.setSearchKeyword(this.tempSearchKeyword)
    },
    searchKeyword(){
      if (this.tempSearchKeyword!=this.searchKeyword){
        this.tempSearchKeyword=this.searchKeyword
      }
    }
  },
  components: {
    navbar
  }
}



